import Vue from 'vue'
import Vuex from 'vuex'
import playerData from './playerData'
import lang from './lang'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    game: 'testtt',
  },
  mutations: {
    setGame: (state, payload) => {
      state.game = payload
    },
  },
  actions: {},
  getters: {
    // isAuth: (state, getters) => getters['lang/lang'],
    game: (state) => state.email,
    // lang: (state) => state.lang.lang,
  },
  modules: {
    playerData,
    lang
  },
})

export default store
