<template>
  <div
    class="card"
    @click='open'
  >
    <div class="image"
      :class='[
        {_level_2: game == `series`},
        {_level_3: game == `heroes`},
        {_level_4: game == `bonus`},
      ]'
    ></div>

    <div v-if='!isOpen' class="bottom">
      <span class="title">{{title}} ({{points}}/{{total}})</span>
    </div>

    <div v-else-if='!isShowStartBtn' class="btns">
      <span class="btn" @click='startFromBegin'>{{$t('menu.btns.start')}}</span>
    </div>

    <div v-else class="btns">
      <span class="btn" @click='startFromBegin'>{{$t('menu.btns.begin')}}</span>
      <span v-if='isShowContinueBtn' class="btn" @click='startFromSaved'>{{$t('menu.btns.continue')}}</span>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import SButton from '@/components/Main/SButton.vue'

export default {
  name: 'Card',
  components: {
    SButton,
  },
  props: {
    game: String,
    title: String,
    points: [Number, String],
    total: Number,
  },
  data() {
    return {
      isOpen: false,
    }
  },
  mounted() {
  },
  methods: {
    open() {
      this.isOpen = !this.isOpen;
    },
    startFromBegin() {
      this.showAds()

      try {
        window.localStorage.setItem(this.game, 0)
      } catch (e) {
        console.log('error:', e)
      }

      this.$router.push({ name: 'main', params: { name: this.game } })
    },
    startFromSaved() {
      this.showAds()
      this.$router.push({ name: 'main', params: { name: this.game, points: this.points } })
    },
    showAds() {
      this.$ysdk.adv.showFullscreenAdv()
    },
  },
  computed: {
    isShowStartBtn() {
      return this.points > 0;
    },
    isShowContinueBtn() {
      return this.points < this.total;
    },
  },
  watch: {
  },
}
</script>

