<template>
  <main class='menu'>
    <div class="langs">
      <SButton
        text='Рус'
        @click.native='$root.$i18n.locale = `ru`'/>
      <SButton
        text='Eng'
        @click.native='$root.$i18n.locale = `en`'/>
    </div>
    <div class="about">
      {{$t('title')}}
    </div>
    <div class="cards">
      <Card game='minecraft' :title='$t(`menu.cards.title_1`)' :points='getPoints("minecraft")' :total='getTotalLevels("minecraft")'/>
      <Card game='series' :title='$t(`menu.cards.title_2`)' :points='getPoints("series")' :total='getTotalLevels("series")'/>
      <Card game='heroes' :title='$t(`menu.cards.title_3`)' :points='getPoints("heroes")' :total='getTotalLevels("heroes")'/>
      <Card game='bonus' :title='$t(`menu.cards.title_4`)' :points='getPoints("bonus")' :total='getTotalLevels("bonus")'/>
    </div>
    <a :href="link" target='_blank' class='link'>{{ $t('link') }}</a>
  </main>
</template>

<script>
// @ is an alias to /src
import Card from '@/components/Menu/Card.vue'
import SButton from '@/components/Main/SButton.vue'

export default {
  name: 'Main',
  components: {
    Card,
    SButton,
  },
  data() {
    return {
    }
  },
  mounted() {
    console.log('this.$ysdk', this.$ysdk)
  },
  methods: {
    getPoints(name) {
      let points = 0

      try {
        points = Number(window.localStorage.getItem(name))
      } catch (e) {
        console.log('error:', e)
      }

      if (points) {
        return points
      }

      return 0
    },
    getTotalLevels(name) {
      if (name == 'minecraft') {
        return 32
      }

      if (name == 'series') {
        return 60
      }

      if (name == 'heroes') {
        return 8
      }

      if (name == 'bonus') {
        return 8
      }

      return 0
    },
  },
  computed: {
    link() {
      return `https://yandex.${this.$ysdk.environment.i18n.tld}/games/developer?name=Kagari%20Games`;
    },
  },
  watch: {
  },
}
</script>

