import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import './styles/reset.css'

import VueI18n from 'vue-i18n'
import locales from './helpers/locales.js'

Vue.use(VueI18n)

const i18n = new VueI18n({
  locale: 'ru',
  fallbackLocale: ['en','ru'],
  messages: locales, // установка сообщений локализаций
})

// Init YandexSDK
window.YaGames.init()
.then(_ysdk => {
    Vue.prototype.$ysdk = _ysdk;
    if (_ysdk?.environment.i18n.lang != 'ru') {
      i18n.locale = 'en'
    }
    return _ysdk;
})
.finally(() => {
  Vue.config.productionTip = false
  new Vue({
    i18n,
    router,
    store,
    render: h => h(App)
  }).$mount('#app')
})

Vue.config.productionTip = false
new Vue({
  i18n,
  router,
  store,
  render: h => h(App)
}).$mount('#app')