export default {
  namespaced: true,
  state: {
    data: null,
  },
  mutations: {
    setPlayerData(state, data) {
      state.data = {...data}
    },
  },
  actions: {
    updatePlayersData({ commit }, data) {
      commit('setPlayerData', data)
    },
  },
  getters: {
    playerData(state) {
      return state.data
    },
  },
}
